// src/components/js/Header.js

import React, { useState, useEffect } from 'react';
import './../../components/css/Header.css';
import BTECLogo from './../../assets/btec-logo.png.png'; // Logo BTEC (mu cho nn sng)
import BTECLogoWhite from './../../assets/btec-logo.png.png'; // Logo BTEC trng (cho nn ti khi cun)

function Header() {
  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 50) {
        setIsScrolled(true);
      } else {
        setIsScrolled(false);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  return (
    <header className={`header ${isScrolled ? 'scrolled' : ''}`}>
      <div className="container">
        <div className="logo-section">
          <img
            src={isScrolled ? BTECLogoWhite : BTECLogo}
            alt="BTEC International College Logo"
            className="btec-logo"
          />
          <span className="header-slogan">
            {isScrolled ? '' : 'Ni Tinh Hoa Hi T'}
          </span>
        </div>
        <nav className="navbar">
          <ul>
            <li><a href="#home">Trang Ch</a></li>
            <li><a href="#intro">Gii Thiu</a></li>
            <li><a href="#summer">Ma H BTEC</a></li>
            <li><a href="#contact">Lin H</a></li>
          </ul>
        </nav>
        <button className="get-quote-btn">Nhn T Vn</button>
      </div>
    </header>
  );
}

export default Header;